<template>
  <!-- search-input -->
  <div class="search-ipt-wp">
    <van-cell-group>
      <van-field v-model.trim="name" @input="search" class="search-ipt"/>
      <div class="placeholder-wp" v-if="showPlaceholder">
        <van-icon name="search" class="search-icon"></van-icon>
        <span class="placeholder">{{_placeholder}}</span>
      </div>
    </van-cell-group>
  </div>
</template>


<script>
export default {
  props: {
    _placeholder: {
      default: '请输入终端名称',  
    },
    // 上一次查询的name
    _name: {
      default: '',
    },
  },
  data() {
    return {
      name: '',

      showPlaceholder: true,
    }
  },
  methods: {
    search() {
      this.$emit('searchChange', this.name);
    }
  },
  watch: {
    _name: {
      handler(value) {
        if(value) {
          this.name = value
        }
        return false;
      },
      immediate: true
    },
    name: {
      handler(value) {
        if(value) {
          this.showPlaceholder = false;
        } else {
          this.showPlaceholder = true;
        }
      },
      immediate: true
    }
  }
}
</script>


<style lang="scss">
.search-ipt-wp{
  position: relative;
  overflow: hidden;
  margin: 15px 40px;
  height: 34px;
  background: transparent;
  border-bottom: 1px solid #999;
  .placeholder-wp{
    position: absolute;
    pointer-events: none;
    z-index: 5;
    left: 50%;
    top: 50%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    font-size: 14px;
    color: #b5b5b4;
    transform: translate(-50%, -50%);
    .search-icon{
      padding-right: 5px;
    }
  }
  .van-cell,
  .van-cell-group,
  .van-field__control {
    background: transparent;
  }
  .search-ipt::after{
    content: none;
  }
}
</style>



